export default {
    props: {
        shops: {
            type: Array,
            default: [{
                title: '',
                left: '',
                status: 0,
                img: ''
            }]
        },
        total:{
            type:Number,
            default:0
        }
    },
    data() {
        return {
            states: [{
                id: 'busy',
                name: '营业中'
            }, {
                id: 'close',
                name: '已打烊'
            }, {
                id: 'exc',
                name: '已过期'
            }, {
                id: 'examing',
                name: '审核中'
            }, {
                id: 'disable',
                name: '弃审'
            }, {
                id: 'disable',
                name: '未购买'
            }, {
                id: 'disable',
                name: '已禁用'
            }]
        }
    },
    methods: {
        bgGray(shop) {
            return (this.shop.id == 'disable' || this.shop.id == 'examing') ? 'background:"#f4f4f4"' : '';
        },
        select(item) {
            console.log(item)
            if (item.statusText == '营业中' || item.statusText == '已打烊' || item.statusText == '已过期') { //营业中、已打烊、已过期的店铺，点击进入小程序
                this.$emit('click', item)
            } else {
                this.Dialog.alert({
                    title: '请登录PC端后台管理此店铺',
                    message: '小程序仅支持管理“营业中”、“已打烊”和“已过期”状态的店铺'
                }).then(() => {
                    // on close
                });
            }
        }
    },
}
